import {Component} from '@angular/core';
import {Headers, Http} from "@angular/http";
import {Observable} from "rxjs";
import 'rxjs/add/operator/map'
import {FormGroup, FormBuilder, Validators} from "@angular/forms";
import {simpleHeaders, contentHeaders} from "./common/headers";


@Component({
  selector: 'mtbls-adv-search',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {


  public searching: boolean = false;
  public resolved: boolean = false;
  public somethingWrong: boolean = false;
  public noResults: boolean = false;
  public noMTBLSButOtherResults: boolean = false;
  private orcidDevSearchURL: string = "//www.ebi.ac.uk/ebisearch/ws/rest/europepmc?query=ORCID:";
  private orcidSearchFormat: string = "&format=json";
  private pmidDevSearchURL: string = "//www.ebi.ac.uk/ebisearch/ws/rest/europepmc/entry/";
  private pmidSearchFormat = "/xref/metabolights?format=json";
  public batchClaimURL = "//www.ebi.ac.uk/ebisearch/search.ebi?db=metabolights&query=domain_source:metabolights";

  private mtblsISAEndpoint = "//www.ebi.ac.uk/metabolights/SwaggerProxy?url=mtbls/ws/studies/";
  private apiToken = localStorage.getItem("apiToken");

  public mtbls_ids: any[] = [];

  public title: string = "";
  public description: string = "";
  public freeText: string = "";

  public searchEditForm: FormGroup;
  private formBuilder: FormBuilder;
  public orcidPatternRegex = "\d\d\d\d-\d\d\d\d-\d\d\d\d-\d\d\d\d";
  private ebiSearchResponse: string = "";


  constructor(private http: Http, fb: FormBuilder) {
    this.formBuilder = fb;
    this.searchEditForm = this.formBuilder.group({
      'searchterm': [this.freeText, Validators.compose([Validators.required, Validators.minLength(19)])]
    })
  }


  onSearch(freeText: string): void {
    this.searching = true;
    this.isValid(freeText);
    let ebiORCIDsearchQuery: string = this.orcidDevSearchURL + freeText + "&start=0&size=99" +this.orcidSearchFormat;
    this.getSimpleRequest(ebiORCIDsearchQuery)
      .subscribe(
        response => {
          console.log(response);
          if (response.length != 0) {
            if (response['hitCount'] > 0) {
              let pmids: number[] = this.extractPMIDS(response);
              this.searchForPMIDS(pmids, freeText);
            }
            else {
              this.noResults = true;
              this.resolved = true;
            }
          }
        },
        error => {
          console.log(error);
          this.somethingWrong = true;
        }
      );


  }

  searchForPMIDS(pmids: number[], freetext: string) {
    let searchString = this.constructSearchString(pmids);
    let ebiPMIDsearchQuery: string = this.pmidDevSearchURL + searchString + this.pmidSearchFormat;

    this.getSimpleRequest(ebiPMIDsearchQuery)
      .subscribe(
        response => {
          console.log(response);
          if (response.length != 0) {
            this.ebiSearchResponse = response;
            //todo place extract mtbls method here
            this.queryMetabolights(freetext);
          }
          else {
            this.resolved = true;
          }
        },
        error => {
          console.log(error);
          this.somethingWrong = true;
          this.resolved = true;
        }
      );
  }


  doUpdate() {
    this.somethingWrong = false;
    this.searching = false;
    this.resolved = false;
    this.mtbls_ids = [];
    this.noResults = false;
    this.noMTBLSButOtherResults = false;
  }

  getSimpleRequest(url): Observable<any> {
    return this.http.get(url, {headers: simpleHeaders}).map(res => res.json());
  }

  getRequest(url, apiToken): Observable<any> {
    contentHeaders.delete('user_token');
    contentHeaders.append('user_token', apiToken);
    return this.http.get(url, {headers: contentHeaders}).map(res => res.json());
  }

  extractPMIDS(searchResponse: any): number[] {
    let entries: any[] = searchResponse["entries"];
    let pmids: number[] = [];
    if (entries.length > 0) {
      for (let i in entries) {
        let id: number = entries[i]["id"];
        pmids.push(id);
      }
    }
    return pmids;
  }

  constructSearchString(pmids: number[]): any {
    let searchString: string = "";
    if (pmids.length == 1) {
      return pmids[0];
    }
    for (let i in pmids) {
      searchString += pmids[i] + ",";
    }
    searchString = searchString.slice(0, -1);
    return searchString;
  }

  extractMtblsIDs(searchResponse: any): any {
    let entries: any[] = searchResponse["entries"];
    for (let i in entries) {
      let references: any[] = entries[i]["references"];
      if (references.length > 0) {
        for (let j in references) {
          let mtbls_id = references[j]["id"];
          this.mtbls_ids.push(mtbls_id);
        }
      }
    }
    if (this.mtbls_ids.length == 0) {
      this.noMTBLSButOtherResults = true;
    }
    this.resolved = true;
  }

  getStudyTitle(studyID: string): any {
    let fetchTitleUrl: string = this.mtblsISAEndpoint + studyID + "/title";
    let title: string = "";
    this.getRequest(fetchTitleUrl, this.apiToken).subscribe(
      response => {
        this.title = response['Study-title'];
      },
      error => {
        console.log(error);
      }
    );
  }

  getStudyDescription(studyID: string): any {
    let fetchDescriptionUrl: string = this.mtblsISAEndpoint + studyID + "/description";
    let description: string = "";
    this.getRequest(fetchDescriptionUrl, this.apiToken).subscribe(
      response => {
        this.description = response['Study-description'];
      },
      error => {
        console.log(error);
      }
    );
  }

  isValid(orcid: string): any {
   return orcid.match(this.orcidPatternRegex);
  }

  queryMetabolights(orcid: string): any {
    let fetchIDsUrl: string = "//www.ebi.ac.uk/ebisearch/ws/rest/metabolights?query=ORCID:"+ orcid + "&format=json";
    this.getSimpleRequest(fetchIDsUrl).subscribe(
      response => {
        this.extractMtblsIDs(this.ebiSearchResponse);
        if (response['hitCount'] != 0) {
          let entries : any[] = response['entries'];
          for(let i in entries){
             let mtbls_id = entries[i]['id'];
             console.log("from ML search = " + mtbls_id);
             let foundAlready = this.mtbls_ids.indexOf(mtbls_id) > -1;
             if(!foundAlready){
                this.mtbls_ids.push(mtbls_id);
             }
          }
        }
      },
      error => {
        console.log(error);
      }
    );
  }
}



