import {Component, OnInit, Input} from '@angular/core';
import {Headers, Http} from "@angular/http";
import {Observable} from "rxjs";
import {simpleHeaders} from "../common/headers";

@Component({
  selector: 'app-mtbls-entry',
  templateUrl: './mtbls-entry.component.html',
  styleUrls: ['./mtbls-entry.component.css']
})
export class MtblsEntryComponent implements OnInit {

  @Input() public studyID: string;
  public title: string;
  public description: string;
  public claimed: boolean = false;

  constructor(private http: Http) { }

  ngOnInit() {
    this.getTitle(this.studyID);
    this.getDescription(this.studyID);
    this.isClaimedToORCID(this.studyID);
  }

  getTitle(studyID: string) {
    let fetchTitleUrl: string = "//www.ebi.ac.uk/ebisearch/ws/rest/metabolights?query=id:" + studyID + "&fields=id,name&format=json"
    this.getSimpleRequest(fetchTitleUrl).subscribe(
      response => {
        if (response['hitCount'] == 1) {
          let entries: any[] = response["entries"];
          for (let i in entries) {
            let entry = entries[i];
            let titles: string[] = entry['fields']['name'];
            if (titles.length > 0) {
              this.title = titles[0];
            }
          }

        }
      },
      error => {
        console.log(error);
      }
    );
  }

  getDescription(studyID: string) {
    let fetchTitleUrl: string = "//www.ebi.ac.uk/ebisearch/ws/rest/metabolights?query=id:" + studyID + "&fields=id,description&format=json"
      this.getSimpleRequest(fetchTitleUrl).subscribe(
      response => {
        if (response['hitCount'] == 1) {
          let entries: any[] = response["entries"];
          for (let i in entries) {
            let entry = entries[i];
            let descriptions: any[] = entry['fields']['description'];
            if (descriptions.length > 0) {
              this.description = descriptions[0];
            }
          }

        }
      },
      error => {
        console.log(error);
      }
    );
  }

  getSimpleRequest(url): Observable<any> {
    return this.http.get(url, {headers: simpleHeaders}).map(res => res.json());
  }

  isClaimedToORCID(studyID : string){
     let claimCheckUrl : string = "//www.ebi.ac.uk/ebisearch/ws/rest/orcid_data_claims?query=METABOLIGHTS:" + this.studyID + "&format=JSON";
    this.getSimpleRequest(claimCheckUrl).subscribe(
      response => {
        if (response['hitCount'] != 0) {
          this.claimed = true;
        }
      },
      error => {
        console.log(error);
      }
    );
  }



}



